// src/components/EssayEditor.tsx
"use client";

import * as React from "react";
import { useEssayAutosave } from "@/hooks/useEssayAutosave";

export type TaskType = "task1" | "task2";

interface EssayEditorProps {
  taskType: TaskType;
  questionText: string;
  isSubmitting: boolean;
  onSubmit: (essay: string) => void;
}

export const EssayEditor: React.FC<EssayEditorProps> = ({
  taskType,
  questionText,
  isSubmitting,
  onSubmit,
}) => {
  const { value, onChange, lastSavedAt, clearDraft } = useEssayAutosave({
    taskType,
    questionText,
    enabled: true,
  });

  const handleSubmit = () => {
    if (!value.trim()) {
      alert("Please write your essay before submitting.");
      return;
    }
    onSubmit(value);
  };

  const savedLabel = lastSavedAt
    ? `Draft saved • ${lastSavedAt.toLocaleTimeString()}`
    : "Draft not yet saved";

  return (
    <div className="flex flex-col gap-3">
      <div className="rounded-md border border-slate-200 bg-white p-3 shadow-sm">
        <p className="text-xs font-medium uppercase tracking-wide text-slate-500">
          {taskType === "task1" ? "Academic Writing Task 1" : "Academic Writing Task 2"}
        </p>
        <p className="mt-2 text-sm text-slate-800 whitespace-pre-line">
          {questionText}
        </p>
      </div>

      <textarea
        className="w-full min-h-[260px] rounded-md border border-slate-300 bg-white p-3 text-sm text-slate-900 shadow-sm outline-none focus:border-sky-500 focus:ring-2 focus:ring-sky-200"
        placeholder="Write your IELTS essay here..."
        value={value}
        onChange={onChange}
      />

      <div className="flex items-center justify-between text-xs text-slate-500">
        <span>{savedLabel}</span>
        <button
          type="button"
          onClick={clearDraft}
          className="underline decoration-dotted underline-offset-2 hover:text-slate-700"
        >
          Clear draft
        </button>
      </div>

      <div className="flex justify-end">
        <button
          type="button"
          onClick={handleSubmit}
          disabled={isSubmitting}
          className="inline-flex items-center rounded-md bg-sky-600 px-4 py-2 text-sm font-medium text-white shadow-sm hover:bg-sky-700 disabled:cursor-not-allowed disabled:opacity-60"
        >
          {isSubmitting ? "Scoring your essay…" : "Submit for scoring"}
        </button>
      </div>
    </div>
  );
};
